</template>

<script>
import request from "@/utils/request";

export default {
  created() {
    this.user = sessionStorage.getItem("user") ? JSON.parse(sessionStorage.getItem("user")) : {};
  },
  data () {

    return {
      user:{},
      radio1: 1,
      radio2: 2,
      radio3: 3,
      radio4: 4,
      radio5: 5,
      radio6: 6,
      radio7: 7,
      radio8: 8,
      radio9: 9,
      radio10: 10,
      radioStyle: {
        display: "block",
        height: "30px",
        lineHeight: "30px"
      }
    };
  },
  methods:{
    submit(){
      let mydate =new Date()
      request.post("/test", {username:this.user.username,time:mydate.getFullYear()+"-"+mydate.getMonth()+"-"+mydate.getDate()+" "+mydate.getHours()+":"+mydate.getMinutes()+":"+mydate.getSeconds(),score:50}).then(res => {
        this.$message({
          type: "success",
          message: "您已经完成本次心理测试"
        })
        this.$router.push('/front/testinfo')
      })
  }
}
}
</script>

<style>
#test {
  margin: 0 0;
}

